#movie {
  padding: 50px 0;
  width: 100%;
  display: flex;
  justify-content: center;
}

.imgWrap {
  width: calc(30% - 50px);
  margin-right: 50px;
}

.movieTxt {
  width: 70%;
}

.title {
  padding-bottom: 20px;
}

.title > a {
  color: #ddd;
  transition: color 0.3s;
  line-height: 1;
}

.title > a:hover {
  color: #999;
}

.gen {
  padding: 5px 10px;
  margin-bottom: 10px;
  list-style: none;
  display: inline-block;
  color: #ccc;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-weight: 700;
  margin-right: 10px;
  font-size: 12px;
}

.summary {
  text-overflow: ellipsis;
}
